webpackJsonp([281252473167557],{

/***/ 423:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Mike Herchel","author":"Mike Herchel"}},"markdownRemark":{"id":"/Users/mherchel/sites/herchel.com/src/blog/d8-theming/index.md absPath of file >>> MarkdownRemark","html":"<h1>Note</h1>\n<p>This is a living document where I throw in notes and code snippits from my experience learning Drupal 8 theme development. There‚Äôs a ton of basic stuff and also a ton of not-so-basic stuff. This isn‚Äôt organized very well‚Ä¶ it‚Äôs more stream-of-concious. Please use <code>CMD+F</code> to navigate around üëç</p>\n<h2>Twig</h2>\n<h3>Basics</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token comment\">{# Prints Label #}</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">label</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">page</span><span class=\"token punctuation\">.</span><span class=\"token property\">footer_fifth</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n <span class=\"token other\"><span class=\"token comment\">&lt;!-- Markup here --></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>Multiple conditions:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token punctuation\">(</span><span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field_first_name</span> <span class=\"token operator\">or</span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field_last_name</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n <span class=\"token other\"><span class=\"token comment\">&lt;!-- Markup here --></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<h3>Ternary Operators in twig</h3>\n<p>Only adds the <code>has-content-right</code> if the <code>page.content_right</code> region has content.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div class=\"layout-container</span> <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">page</span><span class=\"token punctuation\">.</span><span class=\"token property\">content_right</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>has-content-right<span class=\"token punctuation\">'</span></span><span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">\"></span>\n</code></pre>\n      </div>\n<p>You can also add the else statement, which evaluates if the condition is false:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div class=\"layout-container</span> <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">page</span><span class=\"token punctuation\">.</span><span class=\"token property\">content_right</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>has-content-right<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">:</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>no-content-right<span class=\"token punctuation\">'</span></span><span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">\"></span>\n</code></pre>\n      </div>\n<h3>Inheritance</h3>\n<p>Extends syntax says what file it is extending</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">extends</span></span> <span class=\"token string\"><span class=\"token punctuation\">\"</span>block.twig.html<span class=\"token punctuation\">\"</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>Then you define a block with all of the markup inside of it</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">block</span></span> <span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\">Markup and twig syntax goes here.</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endblock</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>Then in the file that gets extended, you need to put a place where the file is getting dropped.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">block</span></span> <span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endblock</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>namespaced paths</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>{% extends \"@classy/block/block.html.twig\" %}</code></pre>\n      </div>\n<h3>Disable caching</h3>\n<p>Uncomment the following in settings.php and move it to the bottom of the file.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token function\">file_exists</span><span class=\"token punctuation\">(</span><span class=\"token variable\">$app_root</span> <span class=\"token punctuation\">.</span> <span class=\"token string\">'/'</span> <span class=\"token punctuation\">.</span> <span class=\"token variable\">$site_path</span> <span class=\"token punctuation\">.</span> <span class=\"token string\">'/settings.local.php'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">include</span> <span class=\"token variable\">$app_root</span> <span class=\"token punctuation\">.</span> <span class=\"token string\">'/'</span> <span class=\"token punctuation\">.</span> <span class=\"token variable\">$site_path</span> <span class=\"token punctuation\">.</span> <span class=\"token string\">'/settings.local.php'</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>Copy <code>example.settings.local.php</code> to <code>settings.local.php</code>.</p>\n<p>Verify the following lines are uncommented to disable render and local page caches.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$config</span><span class=\"token punctuation\">[</span><span class=\"token string\">'system.performance'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">[</span><span class=\"token string\">'css'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">[</span><span class=\"token string\">'preprocess'</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token constant\">FALSE</span><span class=\"token punctuation\">;</span>\n<span class=\"token variable\">$config</span><span class=\"token punctuation\">[</span><span class=\"token string\">'system.performance'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">[</span><span class=\"token string\">'js'</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">[</span><span class=\"token string\">'preprocess'</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token constant\">FALSE</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>Make your <code>development.services.yml</code> file like below. This file is already enabled within the <code>settings.local.php</code> that you‚Äôve just enabled.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token comment\"># Local development services.</span>\n<span class=\"token comment\">#</span>\n<span class=\"token comment\"># To activate this feature, follow the instructions at the top of the</span>\n<span class=\"token comment\"># 'example.settings.local.php' file, which sits next to this file.</span>\n<span class=\"token key atrule\">parameters</span><span class=\"token punctuation\">:</span>\n  <span class=\"token key atrule\">http.response.debug_cacheability_headers</span><span class=\"token punctuation\">:</span> <span class=\"token boolean important\">true</span>\n  <span class=\"token key atrule\">twig.config</span><span class=\"token punctuation\">:</span>\n    <span class=\"token key atrule\">debug</span><span class=\"token punctuation\">:</span> <span class=\"token boolean important\">true</span>\n    <span class=\"token key atrule\">auto_reload</span><span class=\"token punctuation\">:</span> <span class=\"token boolean important\">true</span>\n    <span class=\"token key atrule\">cache</span><span class=\"token punctuation\">:</span> <span class=\"token boolean important\">false</span>\n<span class=\"token key atrule\">services</span><span class=\"token punctuation\">:</span>\n  <span class=\"token key atrule\">cache.backend.null</span><span class=\"token punctuation\">:</span>\n    <span class=\"token key atrule\">class</span><span class=\"token punctuation\">:</span> Drupal\\Core\\Cache\\NullBackendFactory\n</code></pre>\n      </div>\n<h3>Twig stuff</h3>\n<p>AddClass funtion to add CSS class to existing attributes</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>banner<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n</code></pre>\n      </div>\n<p>Filter to run string through the ‚Äòt‚Äô function</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token string\"><span class=\"token punctuation\">'</span>Home<span class=\"token punctuation\">'</span></span><span class=\"token operator\">|</span><span class=\"token property\">t</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<h3>Loops in twig</h3>\n<p><a href=\"https://drupalize.me/tutorial/loops-and-iterators-twig?p=2512\">https://drupalize.me/tutorial/loops-and-iterators-twig?p=2512</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">for</span></span> <span class=\"token property\">item</span> <span class=\"token operator\">in</span> <span class=\"token property\">items</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span> <span class=\"token other\">}} <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">end</span></span> <span class=\"token property\">for</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<p>Check something during the for loop</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">for</span></span> <span class=\"token property\">item</span> <span class=\"token operator\">in</span> <span class=\"token property\">items</span> <span class=\"token keyword\">if</span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">status</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">value</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endfor</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<p>You can add an <code>else</code> statement in the for loop that will resolve if there are no items.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">for</span></span> <span class=\"token property\">item</span> <span class=\"token operator\">in</span> <span class=\"token property\">items</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">else</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span>This list is empty<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endfor</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<p>You also have access to a <code>loop</code> variable within the loop.</p>\n<ul>\n<li><code>loop.index</code>\tThe current iteration of the loop. (1 indexed)</li>\n<li><code>loop.index0</code>\tThe current iteration of the loop. (0 indexed)</li>\n<li><code>loop.revindex</code>\tThe number of iterations from the end of the loop (1 indexed)</li>\n<li><code>loop.revindex0</code>\tThe number of iterations from the end of the loop (0 indexed)</li>\n<li><code>loop.first</code>\tTrue if first iteration</li>\n<li><code>loop.last</code>\tTrue if last iteration</li>\n<li><code>loop.length</code>\tThe number of items in the sequence</li>\n<li><code>loop.parent</code>\tThe parent context</li>\n</ul>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">'</span>blog-post__tags field__items<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">></span></span></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">for</span></span> <span class=\"token property\">item</span> <span class=\"token operator\">in</span> <span class=\"token property\">items</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">loop</span><span class=\"token punctuation\">.</span><span class=\"token property\">first</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n      <span class=\"token other\">&lt;li</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">[</span><span class=\"token string\"><span class=\"token punctuation\">'</span>blog-post__tag<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>first<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">elseif</span></span> <span class=\"token property\">loop</span><span class=\"token punctuation\">.</span><span class=\"token property\">last</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n      <span class=\"token other\">&lt;li</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">[</span><span class=\"token string\"><span class=\"token punctuation\">'</span>blog-post__tag<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>last<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">else</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n      <span class=\"token other\">&lt;li</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>blog-post__tag<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">item</span><span class=\"token punctuation\">.</span><span class=\"token property\">content</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endfor</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<h3>Twig functions</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">random</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">ramdom</span><span class=\"token punctuation\">(</span><span class=\"token number\">10</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">&lt;</span> <span class=\"token number\">5</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n <span class=\"token other\">This will print if number is &lt; 5</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<h4>Drupal specific twig function</h4>\n<ul>\n<li><code>url</code> - generates absolute URL</li>\n<li><code>path</code> - generates relative path</li>\n<li><code>link</code> - create html link</li>\n<li><code>file_url</code></li>\n<li><code>attach_library</code> - attaches library to template.</li>\n</ul>\n<h3>Twig Filters</h3>\n<p>Uppercase the text</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">product</span><span class=\"token operator\">|</span><span class=\"token property\">upper</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<p>Uppercase then reverse text</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">product</span><span class=\"token operator\">|</span><span class=\"token property\">upper</span><span class=\"token operator\">|</span><span class=\"token property\">reverse</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<ul>\n<li><code>length</code> Length of object or array or string <code>{% if product|length == 0 %}</code></li>\n</ul>\n<h3>Drupal Specific Filters</h3>\n<p><a href=\"https://www.drupal.org/docs/8/theming/twig/filters-modifying-variables-in-twig-templates\">https://www.drupal.org/docs/8/theming/twig/filters-modifying-variables-in-twig-templates</a></p>\n<ul>\n<li><code>t</code> - translate</li>\n<li><code>drupal_escape</code></li>\n<li><code>clean_class</code></li>\n<li><code>clean_id</code></li>\n<li><code>format_date</code></li>\n<li><code>raw</code> dangerous</li>\n<li><code>render</code> Useful for when chaining the <code>trim</code> filter after it, or for when checking if the value exists.</li>\n<li><code>safe_join</code> The safe<em>join filter joins several strings together with a supplied separator. `{{ items|safe</em>join(‚Äô, ‚Äô) }}`</li>\n<li><code>without</code>  creates a copy of the renderable array and removes child elements by key specified through arguments passed to the filter. <code>{{ content|without('links') }}</code></li>\n</ul>\n<h3>Twig Tests</h3>\n<p><a href=\"https://twig.symfony.com/doc/1.x/\">https://twig.symfony.com/doc/1.x/</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">loop</span><span class=\"token punctuation\">.</span><span class=\"token property\">index</span> <span class=\"token operator\">is</span> <span class=\"token property\">divisible</span> <span class=\"token property\">by</span><span class=\"token punctuation\">(</span><span class=\"token number\">3</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\">...</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">foo</span><span class=\"token punctuation\">.</span><span class=\"token property\">attribute</span> <span class=\"token operator\">is</span> <span class=\"token property\">same</span> <span class=\"token property\">as</span><span class=\"token punctuation\">(</span><span class=\"token boolean\">false</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n    <span class=\"token other\">the foo attribute really is the 'false' PHP value</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<h3>Debugger Breakpoints</h3>\n<p>Native to Twig:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">dump</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n\n<span class=\"token other\">or</span>\n\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">dump</span><span class=\"token punctuation\">(</span><span class=\"token property\">_context</span><span class=\"token operator\">|</span><span class=\"token property\">keys</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<p><code>_context</code> references the current context and contains all variables passed to the template such as variables sent from theme(), prepared by preprocess, or set in the template. Adding {{ dump() }} without specifying a variable is equivalent to {{ dump(_context) }}.</p>\n<p>with Devel module:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">devel_breakpoint</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<p>From here, look into the <code>$context</code> variable.</p>\n<p>when translating items to the twig file, ignore <code>$content['elements']</code>. Start from <code>[content]</code>.</p>\n<h3>Add classes in Drupal twig</h3>\n<ul>\n<li>Printing <code>&#x3C;div{{ attributes }}></code> works fine.</li>\n<li>Don‚Äôt add additional spacing in the attributes.</li>\n<li>\n<p>Add class</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;article</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>mikeclass<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>Remove class</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">removeClass</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>their-class<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>Remove class via set adn ternary operator</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span>\n<span class=\"token keyword\">set</span></span> <span class=\"token property\">classes</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span>\n<span class=\"token operator\">not</span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">isPublished</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>node--unpublished<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span>\n<span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>add attribute</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">setAttribute</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>data-bundle<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">bundle</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>Check if attribute exists</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">attribute</span><span class=\"token punctuation\">.</span><span class=\"token property\">hasClass</span><span class=\"token punctuation\">(</span><span class=\"token string\"><span class=\"token punctuation\">'</span>myClass<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token comment\">{# do stuff #}</span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>Add multiple classes:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span>\n<span class=\"token keyword\">set</span></span> <span class=\"token property\">classes</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span>\n<span class=\"token string\"><span class=\"token punctuation\">'</span>node<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token string\"><span class=\"token punctuation\">'</span>node--type-<span class=\"token punctuation\">'</span></span> <span class=\"token operator\">~</span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">bundle</span><span class=\"token operator\">|</span><span class=\"token property\">clean_class</span><span class=\"token punctuation\">,</span>\n<span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">isPromoted</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>node--promoted<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">isSticky</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>node--sticky<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token operator\">not</span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">isPublished</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>node--unpublished<span class=\"token punctuation\">'</span></span><span class=\"token punctuation\">,</span>\n<span class=\"token property\">view_mode</span> <span class=\"token operator\">?</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>node--view-mode-<span class=\"token punctuation\">'</span></span> <span class=\"token operator\">~</span> <span class=\"token property\">view_mode</span><span class=\"token operator\">|</span><span class=\"token property\">clean_class</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span>\n<span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\">&lt;article</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span><span class=\"token punctuation\">.</span><span class=\"token property\">addClass</span><span class=\"token punctuation\">(</span><span class=\"token property\">classes</span><span class=\"token punctuation\">)</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n</code></pre>\n      </div>\n</li>\n</ul>\n<h2>Twig Notes</h2>\n<p>You can reference the path to the theme with</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">base_path</span> <span class=\"token operator\">~</span> <span class=\"token property\">directory</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<p>Concatenate strings with a <code>~</code></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">base_path</span> <span class=\"token operator\">~</span> <span class=\"token property\">directory</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n</code></pre>\n      </div>\n<h2>Preprocess function notes</h2>\n<p>Use <code>themename.theme</code> for the file.</p>\n<p>Function is in format of <code>function THEME_preprocess_HOOK()</code>. Get the hook from twig debug mode.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/theme_hook_name-4fba98601e3ff1de8e5010a1b8788a59-635a5.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 706px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 26.912181303116146%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Theme Hook Name\"\n        title=\"\"\n        src=\"/static/theme_hook_name-4fba98601e3ff1de8e5010a1b8788a59-635a5.png\"\n        srcset=\"/static/theme_hook_name-4fba98601e3ff1de8e5010a1b8788a59-6b946.png 200w,\n/static/theme_hook_name-4fba98601e3ff1de8e5010a1b8788a59-3ad6a.png 400w,\n/static/theme_hook_name-4fba98601e3ff1de8e5010a1b8788a59-635a5.png 706w\"\n        sizes=\"(max-width: 706px) 100vw, 706px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>You can also use <code>function THEME_preprocess(&#x26;$variables, $hook)</code>. This is useful if you want to inject a variable into every template.</p>\n<p>Use render arrays instead of hardcoding html</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token keyword\">function</span> <span class=\"token function\">icecream_preprocess_node</span><span class=\"token punctuation\">(</span><span class=\"token operator\">&amp;</span><span class=\"token variable\">$variables</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'simple_string'</span><span class=\"token punctuation\">]</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">array</span><span class=\"token punctuation\">(</span>\n    <span class=\"token string\">'#markup'</span> <span class=\"token operator\">=</span><span class=\"token operator\">></span> <span class=\"token string\">'A simple string'</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>string<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span></span>\n <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">simple_string</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<h2>Render API</h2>\n<ol>\n<li>Structured arrays with data and hints on how to render it.</li>\n<li>Pipeline: process Drupal goes through to serve a request.</li>\n</ol>\n<h3>Render Arrays</h3>\n<ul>\n<li>\n<p>Properties are array items where the key‚Äôs names start with a hash. They determine how an instance of an element will ultimately be rendered.</p>\n</li>\n<li>\n<p>Elements are items that do <em>not</em> start with a hash. They can have children. It‚Äôs an individual section of the array and represent data.</p>\n</li>\n<li>\n<p><code>#markup</code> Provides HTML directly</p>\n</li>\n<li>\n<p><code>#theme</code> Provides information on which template is needed to generate the HTML (and where to find it).</p>\n</li>\n<li>\n<p><code>#type</code> Shorthand for writing a more complex element.</p>\n</li>\n</ul>\n<h4>Value properties</h4>\n<p>Store strings, integers, booleans, arrays. Examples:</p>\n<ul>\n<li><code>#prefix</code></li>\n<li><code>#suffix</code></li>\n<li><code>#title</code></li>\n<li><code>#weight</code></li>\n</ul>\n<h4>Callable properties</h4>\n<p>Less common. These hold references to PHP callables. At specific points in the rendering, these can be called to alter the state.</p>\n<ul>\n<li><code>#pre_render</code></li>\n<li><code>#post_render</code></li>\n</ul>\n<h2>Composer</h2>\n<ul>\n<li><code>composer install</code> - initial install. If a <code>composer.lock</code> file exists, it will ignore the <code>package.json</code> and use the lock file.</li>\n<li>\n<p><code>composer update</code> - Updates with the latest tags off of the <code>package.json</code> file.</p>\n<ul>\n<li>You can use <code>composer update [package]</code> to only update a single package.</li>\n</ul>\n</li>\n<li><code>composer require</code> - installs new package (or module), and writes it to the <code>package.json</code>.</li>\n</ul>\n<h2>Tips</h2>\n<ul>\n<li>\n<p>Strip HTML tags with <code>{{ content.field_first_name|render|striptags }}</code></p>\n<ul>\n<li>You can also try using the node object with the <code>getValue()</code> method. That looks like {{ node.field<em>first</em>name.value }}. This shouldn‚Äôt use the field template.</li>\n</ul>\n</li>\n<li>\n<p>Loop through field that has multiple values, and affect markup with</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">for</span></span> <span class=\"token property\">key</span><span class=\"token punctuation\">,</span> <span class=\"token property\">interest</span> <span class=\"token operator\">in</span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field_personal_interests</span> <span class=\"token keyword\">if</span> <span class=\"token property\">key</span><span class=\"token operator\">|</span><span class=\"token property\">first</span> <span class=\"token operator\">!=</span> <span class=\"token string\"><span class=\"token punctuation\">'</span>#<span class=\"token punctuation\">'</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span><span class=\"token punctuation\">></span></span></span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">interest</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endfor</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n</li>\n<li>\n<p>Add jQuery and Drupal behaviors to themename.libraries.yml</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token key atrule\">base</span><span class=\"token punctuation\">:</span>\n<span class=\"token key atrule\">version</span><span class=\"token punctuation\">:</span> <span class=\"token number\">1.0</span>\n<span class=\"token key atrule\">css</span><span class=\"token punctuation\">:</span>\n<span class=\"token key atrule\">theme</span><span class=\"token punctuation\">:</span>\n  <span class=\"token key atrule\">css/styles.css</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> <span class=\"token key atrule\">weight</span><span class=\"token punctuation\">:</span> <span class=\"token number\">10 </span><span class=\"token punctuation\">}</span>\n<span class=\"token key atrule\">js</span><span class=\"token punctuation\">:</span>\n<span class=\"token key atrule\">js/scripts.js</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span>\n<span class=\"token key atrule\">dependencies</span><span class=\"token punctuation\">:</span>\n<span class=\"token punctuation\">-</span> core/jquery\n<span class=\"token punctuation\">-</span> core/drupal\n</code></pre>\n      </div>\n<p>Note the <code>- core/drupal</code> line is necessary to include <code>drupal.js</code> which processes Drupal behaviors.</p>\n</li>\n</ul>\n<h3>Built in CSS classes</h3>\n<p><a href=\"https://www.drupal.org/node/2022859\">See the change record</a></p>\n<ul>\n<li><code>hidden</code> - Does a <code>display: none;</code></li>\n<li><code>visually-hidden</code> - Hides visually, but reachable via screen reader</li>\n<li><code>visually-hidden focusable</code> - Same as above but focusable.</li>\n<li><code>invisible</code> - does a <code>visibility: hidden;</code></li>\n</ul>\n<h3>Include a twig template file</h3>\n<p>Note that the <code>@theme_name</code> points to the <em>templates</em> directory within the <code>theme_name</code> theme!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">include</span></span> <span class=\"token string\"><span class=\"token punctuation\">\"</span>@theme_name/_prospect_circle_image.twig<span class=\"token punctuation\">\"</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>It‚Äôs good practice to prefix your templates with an underscore.</p>\n<h2>Printing out Unix date, NID, sticky, etc</h2>\n<p>There‚Äôs a bunch of <code>getXXX</code> methods under the node object that are not viewable while PHP debugging. You can call these with <code>{{ node.XXX.value }}</code>.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-378ae.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 57.4585635359116%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Debugging Node Values\"\n        title=\"\"\n        src=\"/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-42603.png\"\n        srcset=\"/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-f931c.png 200w,\n/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-e8031.png 400w,\n/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-42603.png 800w,\n/static/debugging-node-values-a57dd13b544f3ac5d453cff68268e06b-378ae.png 905w\"\n        sizes=\"(max-width: 800px) 100vw, 800px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Examples include</p>\n<ul>\n<li>Created date - <code>{{ node.created.value }}</code></li>\n<li>Updated date - <code>{{ node.updated.value }}</code></li>\n<li>Node ID - <code>{{ node.nid.value }}</code></li>\n<li>UID - <code>{{ node.uid.value }}</code></li>\n<li>Status - <code>{{ node.status.value }}</code></li>\n<li>Node Type - <code>{{ node.type.value }}</code></li>\n</ul>\n<h2>Multiple ways to check if field exists in twig</h2>\n<p>Sometimes doing a simple <code>{% if content.xxx %}</code> doesn‚Äôt correctly check. There‚Äôs an 8 year old (!!!) issue on Drupal.org (<a href=\"https://www.drupal.org/project/drupal/issues/953034\">link</a>) that details the issues. <a href=\"https://www.drupal.org/project/drupal/issues/953034?page=1#comment-12448143\">Mark Carver‚Äôs comment</a> provides some insight on why this is not yet fixed.</p>\n<p>In the meantime, there are multiple ways to do conditionals, that will work around this bug.</p>\n<p>This is the normal way to do things.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>field<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>If that doesn‚Äôt work you can render it</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span><span class=\"token operator\">|</span><span class=\"token property\">render</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>field<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>You can also check the node object, which has the <code>getValue()</code> method, which can be reference in twig by <code>{{ node.field.value }}</code>. This has the added benefit of not using the field template (and associated markup).</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span><span class=\"token punctuation\">.</span><span class=\"token property\">value</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>field<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">node</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span><span class=\"token punctuation\">.</span><span class=\"token property\">value</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>If needed you can strip tags and trim it after render</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span><span class=\"token operator\">|</span><span class=\"token property\">render</span><span class=\"token operator\">|</span><span class=\"token property\">striptags</span><span class=\"token operator\">|</span><span class=\"token property\">trim</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">class</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>field<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span></span>\n    <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n  <span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n<span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">endif</span></span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>You can also use twig syntax similar to the following if needed</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">if</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">field</span><span class=\"token operator\">|</span><span class=\"token property\">render</span><span class=\"token operator\">|</span><span class=\"token property\">striptags</span><span class=\"token operator\">|</span><span class=\"token property\">trim</span> <span class=\"token operator\">is</span> <span class=\"token operator\">not</span> <span class=\"token property\">empty</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<h2>Getting fields (and entity reference fields) within preprocess</h2>\n<p>Thanks to <a href=\"https://www.lullabot.com/about/marcos-cano\">Marcos Cano</a> for helping me out with all of this!</p>\n<p>When debugging in PHPStorm, it‚Äôs really useful to use the <em>Evaluate</em> panel. Prior to this, I was using PHPStorm‚Äôs console tab, but that didn‚Äôt always work!</p>\n<p>Once stopped at a breakpoint, open up the Evaluate panel by right-clicking on the item you want to start with, and select ‚ÄúEvaluate Expression‚Äù.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/debugging-preprocess-3fdd1404b4264a6ec8c3701ac77c9340-3ea4c.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 760px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 58.157894736842096%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"PHPStorm Debugging\"\n        title=\"\"\n        src=\"/static/debugging-preprocess-3fdd1404b4264a6ec8c3701ac77c9340-3ea4c.png\"\n        srcset=\"/static/debugging-preprocess-3fdd1404b4264a6ec8c3701ac77c9340-2e056.png 200w,\n/static/debugging-preprocess-3fdd1404b4264a6ec8c3701ac77c9340-bc962.png 400w,\n/static/debugging-preprocess-3fdd1404b4264a6ec8c3701ac77c9340-3ea4c.png 760w\"\n        sizes=\"(max-width: 760px) 100vw, 760px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>You can get access to all of the fields by using Entity API Methods.</p>\n<p>First load up the node object (note I didn‚Äôt yet test this code)</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span>\n</code></pre>\n      </div>\n<p>Then do a <code>getFields()</code> on it to see the fields</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token function\">getFields</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>You‚Äôll see an array of stuff including fields.</p>\n<p>From there you can use the <code>get()</code> method to get the field</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token function\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">'field_firstname'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p><em>but</em> you can also use what Marcos Cano called <em>magic</em> by using the fieldname as the method. It makes things more readable.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_firstname</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>From there you can try a couple different things based on the field type. Sometimes you can get the value by doing</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_firstname</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token function\">getValue</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<p>A lot of times, you can browse down the object, and look for the key that holds the actual data that you‚Äôre looking for.</p>\n<p>This might be <code>uri</code> if it‚Äôs a <em>link</em> field. Or, it might be <code>value</code> if it‚Äôs a <em>text</em> field. Expand the array in the watch expression and take a look.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token comment\">// a link field might look like</span>\n<span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_firstname</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">uri</span><span class=\"token punctuation\">;</span>\n<span class=\"token comment\">// or if its a text field</span>\n<span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_firstname</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">value</span><span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<h3>Loading field from an entity reference.</h3>\n<p>You‚Äôll probably need to load some data from an entity reference. Here‚Äôs how to do it.</p>\n<p>Use the above methods to bring up the field for the reference:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_reference</span>\n</code></pre>\n      </div>\n<p>Then you need to open the <code>entity</code> object(?). This won‚Äôt show when you‚Äôre looking in the watches panel for whatever reason.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_reference</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">entity</span>\n</code></pre>\n      </div>\n<p>From there you can use the same methods to access the data as we did before.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_reference</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">entity</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_firstname</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">value</span>\n</code></pre>\n      </div>\n<p>And you can even go into different entity references. Here‚Äôs an example that I worked with. It‚Äôs a reference to another node, that has a reference to a file!</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-php\"><code><span class=\"token variable\">$variables</span><span class=\"token punctuation\">[</span><span class=\"token string\">'node'</span><span class=\"token punctuation\">]</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_parent</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">entity</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_show_media</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">entity</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">field_media_image</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">entity</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">uri</span><span class=\"token operator\">-</span><span class=\"token operator\">></span><span class=\"token property\">value</span>\n</code></pre>\n      </div>\n<h2>Theming views output</h2>\n<p>You may have noticed that Twig debug mode does not output the template suggestions for views twig templates. <a href=\"https://www.drupal.org/project/drupal/issues/2118743\">The issue</a> is currently postponed, so it probably won‚Äôt be fixed until 8.8 or 8.7 at the earliest.</p>\n<p>Never fear though! Views template suggestions work similar to what they did in D7.</p>\n<h3>Views twig templates naming</h3>\n<ul>\n<li>Display output: <code>views-view.html.twig</code>, <code>views-view--VIEWNAME.html.twig</code>, <code>views-view--attachment.html.twig</code>, <code>views-view--VIEWNAME--attachment.html.twig</code>, <code>views-view--DISPLAYNAME.html.twig</code>, <code>views-view--VIEWNAME--DISPLAYNAME.html.twig</code>, <code>views-view--default.html.twig</code>, <code>views-view--default.html.twig</code>, <code>views-view--DISPLAYNAME-1.html.twig</code>, <code>views-view--VIEWNAME--DISPLAYNAME-1.html.twig</code></li>\n<li>Style output: <code>views-view-unformatted.html.twig</code>, <code>views-view-unformatted--VIEWNAME.html.twig</code>, <code>views-view-unformatted--DISPLAYNAME.html.twig</code>, <code>views-view-unformatted--VIEWNAME--DISPLAYNAME.html.twig</code>, <code>views-view-unformatted--default.html.twig</code>, <code>views-view-unformatted--default.html.twig</code>, <code>views-view-unformatted--DISPLAYNAME-1.html.twig</code>, <code>views-view-unformatted--VIEWNAME--DISPLAYNAME-1.html.twig</code></li>\n<li>Row style output: <code>views-view-fields.html.twig</code>, <code>views-view-fields--VIEWNAME.html.twig</code>, <code>views-view-fields--DISPLAYNAME.html.twig</code>, <code>views-view-fields--VIEWNAME--DISPLAYNAME.html.twig</code>, <code>views-view-fields--default.html.twig</code>, <code>views-view-fields--default.html.twig</code>, <code>views-view-fields--DISPLAYNAME-1.html.twig</code>, <code>views-view-fields--VIEWNAME--DISPLAYNAME-1.html.twig</code></li>\n<li>Field Content: <code>views-view-field.html.twig</code>, <code>views-view-field--FIELDNAME.html.twig</code>, <code>views-view-field--VIEWNAME.html.twig</code>, <code>views-view-field--VIEWNAME--FIELDNAME.html.twig</code>, <code>views-view-field--DISPLAYNAME.html.twig</code>, <code>views-view-field--DISPLAYNAME--FIELDNAME.html.twig</code>, <code>views-view-field--VIEWNAME--DISPLAYNAME.html.twig</code>, <code>views-view-field--VIEWNAME--DISPLAYNAME--FIELDNAME.html.twig</code>, <code>views-view-field--DISPLAYNAME-1.html.twig</code>, <code>views-view-field--DISPLAYNAME-1--FIELDNAME.html.twig</code>, <code>views-view-field--VIEWNAME--DISPLAYNAME-1.html.twig</code>, <code>views-view-field--VIEWNAME--DISPLAYNAME-1--FIELDNAME.html.twig</code></li>\n</ul>\n<p>When in doubt, fire up a D7 site, and go into the theming section of the view, and see the template suggestions there.</p>\n<h2>Cachability and Twig</h2>\n<p>Drupal 8 has a new caching system that really sets above from other systems. However there‚Äôs a ~bug~ <a href=\"https://www.drupal.org/project/drupal/issues/2660002\">idiosyncrasy</a> in the way twig templates handle caching.</p>\n<p>If you‚Äôre <em>not</em> printing out your templates with the <code>{{ content }}</code> variable. Say</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token other\">&lt;div</span><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">attributes</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span><span class=\"token other\">></span>\n  <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">first_name</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span> <span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{{</span></span> <span class=\"token property\">content</span><span class=\"token punctuation\">.</span><span class=\"token property\">last_name</span> <span class=\"token rd\"><span class=\"token punctuation\">}}</span></span></span>\n<span class=\"token other\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span></span>\n</code></pre>\n      </div>\n<p>the <em>cacheable metadata</em> won‚Äôt be included. This means that if your data changes within the database, the theming system won‚Äôt know to show the newer data.</p>\n<p>The temporary fix is straightforward, add the following line to the bottom of you twig file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-twig\"><code><span class=\"token tag\"><span class=\"token ld\"><span class=\"token punctuation\">{%</span> <span class=\"token keyword\">set</span></span> <span class=\"token property\">catch_cache</span> <span class=\"token operator\">=</span> <span class=\"token property\">content</span><span class=\"token operator\">|</span><span class=\"token property\">render</span> <span class=\"token rd\"><span class=\"token punctuation\">%}</span></span></span>\n</code></pre>\n      </div>\n<p>Thanks to Avi Schwab for [f]inding this solution](<a href=\"https://www.drupal.org/project/drupal/issues/2660002#comment-12361402\">https://www.drupal.org/project/drupal/issues/2660002#comment-12361402</a>) and Mark Conroy for putting it <a href=\"https://mark.ie/blog/web-development/creating-card-component-patternlab-and-mapping-drupal-right-way\">somewhere I actually noticed it</a>.</p>\n<h2>Theming Menus</h2>\n<p>Theming Drupal‚Äôs menus can be a pain in the ass. Tam√°s Hajas has an article <a href=\"https://medium.com/integral-vision/drupal-8-twig-add-custom-css-classes-to-menus-based-on-menu-name-35b50142521a\">https://medium.com/integral-vision/drupal-8-twig-add-custom-css-classes-to-menus-based-on-menu-name-35b50142521a</a> showing how to insert proper BEM classes into your markup effectively. The final iteration of the template can be found at <a href=\"https://gist.github.com/thamas/5c2764d65973721f5413b9540e765510\">https://gist.github.com/thamas/5c2764d65973721f5413b9540e765510</a></p>\n<h2>Template suggestions</h2>\n<p>Casey Wright has an awesome article on this at <a href=\"https://www.chapterthree.com/blog/how-to-create-custom-theme-suggestions-drupal-8\">https://www.chapterthree.com/blog/how-to-create-custom-theme-suggestions-drupal-8</a>, which makes use of <code>HOOK_theme_suggestions_HOOK_alter</code> in preprocess.</p>","excerpt":"Note This is a living document where I throw in notes and code snippits from my experience learning Drupal 8 theme development. There‚Äôs a‚Ä¶","frontmatter":{"title":"D8 Theming Notes","date":"July 17, 2018","author":null}}},"pathContext":{"slug":"/d8-theming/"}}

/***/ })

});


